<template>
  <form class="c-form--small" @submit.prevent="checkEditForm">
    <div class="c-form__group">
      <label for="username" class="c-form__label">
        
        <span class="c-form__notes">20</span>
      </label>
      <input
        id="username"
        v-model="editForm.name"
        type="text"
        class="c-input c-input--large"
        required
        autocomplete="name"
      />
      <invalid-component :messages="nameErrors" />
      <invalid-component
        v-if="editErrors && editErrors.name"
        :messages="editErrors.name"
      />
    </div>
    <div class="c-form__group">
      <label for="email" class="c-form__label"></label>
      <input
        id="email"
        v-model="editForm.email"
        type="email"
        class="c-input c-input--large"
        placeholder="your.email@example.com"
        required
        autocomplete="email"
      />
      <invalid-component :messages="emailErrors" />
      <invalid-component
        v-if="editErrors && editErrors.email"
        :messages="editErrors.email"
      />
    </div>
    <div class="c-form__button">
      <button type="submit" class="c-btn--accent c-btn--large">
        
      </button>
    </div>
  </form>
</template>

<script>
import { mapState } from "vuex"; // VuexmapState
import InvalidComponent from "../components/InvalidComponent.vue";

export default {
  components: {
    InvalidComponent, //
  },
  data() {
    return {
      // v-model
      editForm: {
        name: "",
        email: "",
      },
      nameErrors: [],
      emailErrors: [],
      successMessage: "",
    };
  },
  computed: {
    ...mapState({
      // auth
      userData: (state) => state.auth.userData,
      // authAPI
      apiStatus: (state) => state.auth.apiStatus,
      // auth
      editErrors: (state) => state.auth.editErrorMessages,
    }),
  },
  created() {
    // 
    this.clearError();
    // 
    this.setUserData();
  },
  methods: {
    // 
    checkEditForm() {
      const MSG_NAME_EMPTY = "";
      const MSG_NAME_MAX = "20";
      const MSG_EMAIL_EMPTY = "";
      const MSG_EMAIL_TYPE = "";
      const MSG_EMAIL_MAX = "50";
      this.nameErrors = [];
      this.emailErrors = [];

      // 
      if (!this.editForm.name) {
        // 
        this.nameErrors.push(MSG_NAME_EMPTY);
      } else if (this.editForm.name.length > 20) {
        // 
        this.nameErrors.push(MSG_NAME_MAX);
      }
      // 
      if (!this.editForm.email) {
        // 
        this.emailErrors.push(MSG_EMAIL_EMPTY);
      } else if (this.editForm.email.length > 50) {
        // 
        this.emailErrors.push(MSG_EMAIL_MAX);
      } else if (!this.validEmail(this.editForm.email)) {
        // 
        this.emailErrors.push(MSG_EMAIL_TYPE);
      }
      // 
      const results = this.nameErrors.concat(this.emailErrors);
      // WebAPI
      if (!results.length) {
        this.EditAccount();
      }
    },
    // 
    validEmail(email) {
      const regex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return regex.test(email);
    },
    // WebAPI
    async EditAccount() {
      this.$store.commit("loader/setIsLoading", true); //
      // dispatch()auth
      await this.$store.dispatch("auth/EditAccount", this.editForm);
      this.$store.commit("loader/setIsLoading", false); //
      // API
      if (this.apiStatus) {
        // 
        this.$store.dispatch("message/showMessage", {
          text: "",
          type: "success",
          timeout: 2000,
        });
        // 
        this.nameErrors = [];
        this.emailErrors = [];
      }
    },
    clearError() {
      // 
      this.$store.commit("auth/setEditErrorMessages", null);
    },
    setUserData() {
      // DBv-model
      this.editForm.name = this.userData.name;
      this.editForm.email = this.userData.email;
    },
  },
};
</script>
