<template>
  <div class="p-target">
    <div class="p-target__list">
      <h5 class="p-target__title"></h5>
      <pagination-info
        :current-page="currentPage"
        :per-page="perPage"
        :total-num="totalNum"
        :items-length="targets.length"
      />
      <search-form-component
        :total-num="totalNum"
        :searched-param="searchedParam"
        item-name=""
        @search="searchTargets"
        @clear="clearResult"
      />

      <twitter-target-item
        v-for="target in targets"
        :key="target.id"
        :item="target"
        @follow="createFollow"
        @unfollow="destroyFollow"
      />
      <div v-if="!searchedParam && totalNum === 0" class="u-font--center">
        <p v-if="useAutoFollow"></p>
        <p v-if="!useAutoFollow">
          
        </p>
      </div>

      <div class="c-pagination">
        <pagination-link
          :directory="directoryName"
          :searched-param="searchedParam"
          :current-page="currentPage"
          :last-page="lastPage"
          :per-page="perPage"
          :total-num="totalNum"
        />
        <pagination-info
          :current-page="currentPage"
          :per-page="perPage"
          :total-num="totalNum"
          :items-length="targets.length"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { OK } from "../utility";
import { mapGetters } from "vuex"; // VuexmapGetters
import TwitterTargetItem from "../components/TwitterTargetItem.vue";
import PaginationLink from "../components/PaginationLink.vue";
import PaginationInfo from "../components/PaginationInfo.vue";
import SearchFormComponent from "../components/SearchFormComponent.vue";

export default {
  components: {
    TwitterTargetItem,
    PaginationLink, //PC
    PaginationInfo, //
    SearchFormComponent, //
  },
  // TwitterListComponent
  props: {
    page: {
      type: Number,
      required: false,
      default: 1,
    },
  },
  data() {
    return {
      targets: [], //
      currentPage: 1, //
      lastPage: 1, //
      perPage: 10, //1
      totalNum: 0, //
      directoryName: "twitter", //
      searchedParam: "", // 
    };
  },
  computed: {
    ...mapGetters({
      // twitteruseAutoFollow
      useAutoFollow: "twitter/useAutoFollow",
    }),
  },
  watch: {
    // 
    // $route
    $route: {
      async handler() {
        await this.fetchTargets();
      },
      immediate: true, //
    },
  },
  methods: {
    // 
    searchTargets(word) {
      // 
      if (!word) {
        this.clearResult();
        return;
      }
      let params = {
        // 
        params: {
          search: word,
        },
      };
      // Twitter
      this.fetchTargets(params);
      this.searchedParam = word;
    },
    // 
    clearResult() {
      // 
      let params = {
        params: {
          page: 1,
        },
      };
      this.fetchTargets(params);
      this.searchedParam = "";
    },
    // Twitter
    async fetchTargets(params) {
      this.$store.commit("loader/setIsLoading", true); //
      if (!params) {
        params = {
          params: {
            page: this.page,
            search: this.searchedParam,
          },
        };
      }
      // API
      const response = await axios.get("/api/twitter", params);

      this.$store.commit("loader/setIsLoading", false); //
      // 200
      if (response.status !== OK) {
        this.$store.commit("error/setCode", response.status);
        return false; //
      }
      // JSONdata
      this.targets = response.data.data;

      // 
      this.currentPage = response.data.current_page;
      this.lastPage = response.data.last_page;
      // 1
      this.perPage = response.data.per_page;
      this.totalNum = response.data.total;
      return;
    },
    // 1
    async createFollow(id) {
      this.$store.commit("loader/setIsLoading", true); //
      const response = await axios.post(`/api/twitter/${id}/follow`);
      this.$store.commit("loader/setIsLoading", false); //
      // 200
      if (response.status !== OK) {
        this.$store.commit("error/setCode", response.status);
        return false; //
      }

      // OKLaravel'is_done'true
      if (response.data.is_done) {
        // 
        this.targets = this.targets.map((target) => {
          if (target.twitter_id === response.data.target_id) {
            // true
            target.followed_by_user = true;
          }
          return target;
        });
        // 
        this.$store.dispatch("message/showMessage", {
          text: "",
          type: "success",
          timeout: 2000,
        });
      } else {
        // 'is_done'false
        this.$store.dispatch("message/showMessage", {
          text: response.data.message,
          type: "danger",
          timeout: 2000,
        });
      }
    },
    // 1
    async destroyFollow(id) {
      this.$store.commit("loader/setIsLoading", true); //
      const response = await axios.post(`/api/twitter/${id}/unfollow`);
      this.$store.commit("loader/setIsLoading", false); //
      // 200
      if (response.status !== OK) {
        this.$store.commit("error/setCode", response.status);
        return false; //
      }

      // OKLaravel'is_done'true
      if (response.data.is_done) {
        // 
        this.targets = this.targets.map((target) => {
          if (target.twitter_id === response.data.target_id) {
            // false
            target.followed_by_user = false;
          }
          return target;
        });
        // 
        this.$store.dispatch("message/showMessage", {
          text: "",
          type: "success",
          timeout: 2000,
        });
      } else {
        // 'is_done'false
        this.$store.dispatch("message/showMessage", {
          text: response.data.message,
          type: "danger",
          timeout: 2000,
        });
      }
    },
  },
};
</script>
