<template>
  <form class="c-form--large" @submit.prevent="sendContact">
    <p class="c-form__text"><br /></p>
    <div class="c-form__group">
      <label for="name" class="c-form__label">
        <span class="c-form__label--required"></span>
      </label>
      <div class="c-form__confirm-text">
        {{ formData.name }}
      </div>
    </div>
    <div class="c-form__group">
      <label for="email" class="c-form__label">
        <span class="c-form__label--required"></span>
      </label>
      <div class="c-form__confirm-text">
        {{ formData.email }}
      </div>
    </div>

    <div class="c-form__group">
      <label for="message" class="c-form__label">
        <span class="c-form__label--required"></span>
      </label>
      <div class="c-form__confirm-text">
        <p class="u-font__br">{{ formData.message }}</p>
      </div>
    </div>
    <div class="c-form__button">
      <div class="c-btn--danger u-mb--m" @click.prevent="goBack">
        
      </div>
      <button type="submit" class="c-btn--main u-mb--m"></button>
    </div>
  </form>
</template>

<script>
import { OK, UNPROCESSABLE_ENTITY } from "../utility";

export default {
  props: {
    // v-model
    formData: {
      type: Object,
      required: true,
      //
      default: function () {
        return {
          name: "",
          email: "",
          message: "",
        };
      },
    },
  },
  data() {
    return {
      // 
      apiMessages: [],
      apiResult: "",
      replacedMessage: "", // \n  <br>
    };
  },
  methods: {
    // 
    goBack() {
      this.$emit("back");
      return;
    },
    // WebAPI
    async sendContact() {
      // 
      this.apiMessages = [];
      this.$store.commit("loader/setIsLoading", true); //
      // API
      const response = await axios.post("/api/contact/send", this.formData);
      this.$store.commit("loader/setIsLoading", false); //
      // API
      if (response.status === OK) {
        // $emit
        this.$emit("sent", this.formData);
        return;
      }
      // 
      if (response.status === UNPROCESSABLE_ENTITY) {
        // 
        this.apiMessages = response.data.errors;
        // $emit
        this.$emit("back");
        return;
      } else {
        // errorsetCode
        // commitroot: true
        this.$store.commit("error/setCode", response.status);
        // $emit
        this.$emit("back");
      }
    },
  },
};
</script>
