<template>
  <div>
    <main id="main">
      <page-top />
      <inner-message />
      <loader-component v-show="isLoading" />
      <router-view v-show="!isLoading" />
    </main>
    <footer id="footer">
      <footer-component />
    </footer>
  </div>
</template>

<script>
// app.blade.phpvue.js
// VueRouter
import { NOT_FOUND, UNAUTHORIZED, INTERNAL_SERVER_ERROR } from "./utility";
import FooterComponent from "./components/FooterComponent";
import LoaderComponent from "./components/LoaderComponent";
import PageTop from "./components/PageTop";
import InnerMessage from "./components/InnerMessage";

export default {
  components: {
    FooterComponent, //
    LoaderComponent, //
    PageTop, //
    InnerMessage, //Vuex
  },
  computed: {
    errorCode() {
      // error
      return this.$store.state.error.code;
    },
    isLoading() {
      return this.$store.state.loader.isLoading;
    },
  },
  watch: {
    // watcherrorCode
    errorCode: {
      async handler(val) {
        // 
        if (val === INTERNAL_SERVER_ERROR) {
          this.$store.dispatch("message/showMessage", {
            text:
              "",
            type: "danger",
            timeout: 2000,
          });
        } else if (val === UNAUTHORIZED) {
          // 
          // CSRF
          await axios.get("/api/refresh-token");
          // userData
          this.$store.commit("auth/setUserData", null);
          // 
          this.$router.push({ name: "login" });
        } else if (val === NOT_FOUND) {
          // 404NotFound
          this.$router.push({ name: "errors.notfound" });
        }
      },
      immediate: true, // watch
    },
    $route() {
      // setCodenull
      this.$store.commit("error/setCode", null);
    },
  },
};
</script>
