<template>
  <div :class="{ 'c-fade-in': isVisible }">
    <slot v-show="isVisible" />
  </div>
</template>

<script>
// DOM
export default {
  data() {
    return {
      isVisible: false,
    }
  },
  created() {
    // 
    window.addEventListener('scroll', this.handleScroll)
  },
  destroyed() {
    // 
    window.removeEventListener('scroll', this.handleScroll)
  },
  methods: {
    // DOMisVisibletrue
    handleScroll() {
      // 
      if (!this.isVisible) {
        // 
        var top = this.$el.getBoundingClientRect().top
        // +100px
        // isVisibletrue
        this.isVisible = top < window.innerHeight + 50
      }
    },
  },
}
</script>
