<template>
  <div class="c-container--bg">
    <h2 class="c-container__title"></h2>

    <div class="c-form__wrapper">
      <form class="c-form--large" @submit.prevent="checkForm">
        <div v-if="isSent">
          <p class="c-form__text">
            URL<br
              class="u-sp--hidden"
            /><br />
            <br
              class="u-sp--hidden"
            /><br />
          </p>
        </div>
        <div v-else>
          <p class="c-form__text">
            <br
              class="u-sp--hidden"
            /><br />
            URL<br
              class="u-sp--hidden"
            /><br />
          </p>
          <div class="c-form__group">
            <label for="login-email" class="c-form__label">
              
            </label>
            <input
              id="email"
              v-model="requestForm.email"
              type="text"
              class="c-input c-input--large"
              required
              autocomplete="email"
            />
            <invalid-component :messages="emailErrors" />
            <transition name="popup">
              <p v-if="apiMessage" class="u-mb--l c-alert--danger">
                {{ apiMessage }}
              </p>
            </transition>
          </div>

          <div class="c-form__button">
            <button type="submit" class="c-btn--main-outline c-btn--large">
              
            </button>
          </div>
        </div>
      </form>
      <div class="c-form__link">
        <router-link :to="{ name: 'login' }" class="c-form__link">
          
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { OK } from "../utility";
import InvalidComponent from "../components/InvalidComponent.vue";

export default {
  components: {
    InvalidComponent, //
  },
  data() {
    return {
      // v-model
      requestForm: {
        email: "",
      },
      // 
      emailErrors: [],
      // 
      apiMessage: "",
      isSent: false,
    };
  },
  methods: {
    /**
     * 
     */
    checkForm() {
      const MSG_EMAIL_EMPTY = "";
      const MSG_EMAIL_TYPE = "";
      const MSG_EMAIL_MAX = "50";

      this.emailErrors = [];

      // 
      if (!this.requestForm.email) {
        // 
        this.emailErrors.push(MSG_EMAIL_EMPTY);
      } else if (this.requestForm.email.length > 50) {
        // 
        this.emailErrors.push(MSG_EMAIL_MAX);
      } else if (!this.validEmail(this.requestForm.email)) {
        // 
        this.emailErrors.push(MSG_EMAIL_TYPE);
      }
      // WebAPI
      if (!this.emailErrors.length) {
        this.sendResetLink();
      }
    },

    /**
     * 
     */
    validEmail(email) {
      const regex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return regex.test(email);
    },

    /**
     * WebAPI
     */
    async sendResetLink() {
      this.$store.commit("loader/setIsLoading", true); //
      // API
      const response = await axios.post(
        "/api/password/email",
        this.requestForm
      );
      this.$store.commit("loader/setIsLoading", false); //
      // API
      if (response.status === OK) {
        // Laravelresult
        const result = response.data.result;
        // 
        if (result === "success") {
          // true
          this.isSent = true;
          // 
          window.scrollTo({
            top: 0,
            behavior: "smooth",
          });
          // 
        } else if (result === "failed") {
          // Laravel
          this.apiMessage = response.data.message;
        }
      } else {
        // errorsetCode
        // commitroot: true
        this.$store.commit("error/setCode", response.status);
      }
    },
  },
};
</script>
