<template>
  <transition name="slide-left">
    <div
      v-if="flashMessage && isShow"
      class="c-alert p-message-large"
      :class="`c-alert--${messageType} p-message-large--${messageType}`"
    >
      <p
        class="p-message-large__text"
        :class="`p-message-large__text--${messageType}`"
      >
        {{ flashMessage }}
      </p>
      <i class="fas fa-times p-message-large__close" @click="closeMessage()" />
    </div>
  </transition>
</template>

<script>
export default {
  props: {
    message: {
      type: String,
      required: true,
    },
    type: {
      type: String,
      required: true,
      default: "success",
    },
    timeout: {
      type: String, //BladeString
      required: true,
      default: 3000,
    },
  },
  data() {
    return {
      isShow: true,
      timeoutId: -1,
    };
  },
  computed: {
    // 
    flashMessage() {
      return this.message;
    },
    // CSS
    messageType() {
      return this.type;
    },
  },

  mounted() {
    // DOM
    this.timeoutMessage();
  },

  methods: {
    timeoutMessage() {
      // timeoutId1timeoutIdclearTimeout()
      if (this.timeoutId !== -1) {
        clearTimeout(this.timeoutId);
        // timeoutId
        this.clearTimeoutId();
      }
      // 3
      const timeoutId = setTimeout(() => {
        this.closeMessage();
      }, this.timeout);
      // setTimeout()timeoutId
      this.setTimeoutId(timeoutId);
    },
    // 
    closeMessage() {
      this.isShow = false;
    },
    // timeoutId
    setTimeoutId(value) {
      this.timeoutId = value;
    },
    // timeoutId
    clearTimeoutId() {
      this.timeoutId = -1;
    },
  },
};
</script>
