<template>
  <div class="c-container--bg">
    <section class="c-section">
      <h5 class="c-section__title">
        
      </h5>
      <p class="c-section__text">
        <br class="u-sp--only"><br>
        <br class="u-sp--only"><br>
        <br class="u-sp--only">Twitter<br
          class="u-sp--only"
        ><br>
      </p>
      <div class="c-tab c-fade-in">
        <ul class="c-tab__list">
          <li
            class="c-tab__item c-tab__item--three"
            :class="{ 'c-tab__item--active': column === 'tweet_hour' }"
            @click="sortByHour"
          >
            1<br class="u-sp--only">
          </li>
          <li
            class="c-tab__item c-tab__item--three"
            :class="{ 'c-tab__item--active': column === 'tweet_day' }"
            @click="sortByDay"
          >
            24<br class="u-sp--only">
          </li>
          <li
            class="c-tab__item c-tab__item--three"
            :class="{ 'c-tab__item--active': column === 'tweet_week' }"
            @click="sortByWeek"
          >
            1<br class="u-sp--only">
          </li>
        </ul>

        <div class="p-trend">
          <div class="p-trend__head">
            <div class="p-trend__head-left">
              <span
                v-if="!selectedItems.length || items.length === selectedItems.length"
                class="u-font--small u-font--muted"
              >
                <!-- {{ matchedItems.length }} -->
              </span>
              <span
                v-else
                class="u-font--small u-font--muted"
              > {{ selectedItems.length }} </span>
              <button
                class="c-btn--muted-outline c-btn--small"
                @click="isActive = !isActive"
              >
                <i
                  v-show="!isActive"
                  class="fas fa-angle-down"
                />
                <i
                  v-show="isActive"
                  class="fas fa-angle-up"
                />
                
              </button>
            </div>
            <div class="p-trend__head-right">
              <div class="u-font--small u-font--muted">
                 <br class="u-sp--only">{{ updatedAt }}
              </div>
            </div>
          </div>
          <slide-down-component>
            <div
              v-show="isActive"
              class="p-trend__select"
            >
              <p class="p-trend__select-text">
                
              </p>
              <i
                class="fas fa-times p-trend__select-close"
                @click="isActive = false"
              />
              <ul class="p-trend__select-list">
                <li
                  v-for="item in items"
                  :key="item.id"
                  class="p-trend__select-item"
                >
                  <input
                    :id="item.id"
                    v-model="selectedItems"
                    type="checkbox"
                    class="c-checkbox__icon"
                    :value="item.id"
                  >
                  <label :for="item.id">
                    {{ item.currency_name }}
                  </label>
                </li>
              </ul>
              <div class="p-trend__select-footer">
                <a
                  v-if="selectedItems.length"
                  class="c-btn--muted-outline c-btn--small"
                  @click="deselect()"
                >  </a>
                <a
                  v-else
                  class="c-btn--muted-outline c-btn--small"
                  @click="selectAll()"
                >  </a>
              </div>
            </div>
          </slide-down-component>

          <table class="c-table c-fade-in">
            <thead class="c-table__thead">
              <tr>
                <th class="c-table--left">
                  
                </th>
                <th class="c-table--left">
                  
                </th>
                <th class="c-table--center">
                  {{ activeColumn }}<br>
                </th>
                <th class="c-table--right">
                  24<br class="u-sp-hidden"><br>
                  
                </th>
                <th class="c-table--right">
                  24<br class="u-sp-hidden"><br>
                  
                </th>
              </tr>
            </thead>
            <transition-group
              appear
              tag="tbody"
              name="flip-list"
              class="c-table__tbody"
            >
              <tr
                v-for="trend in matchedItems"
                :key="trend.id"
                class="p-trend__item"
              >
                <td class="p-trend__order-td">
                  <span
                    class="p-trend__order"
                    :class="{
                      'p-trend__order--gold': trend.ranking === 1,
                      'p-trend__order--silver': trend.ranking === 2,
                      'p-trend__order--bronze': trend.ranking === 3,
                    }"
                  >
                    {{ trend.ranking }}
                  </span>
                </td>
                <td class="p-trend__name-td">
                  <a
                    :href="`https://twitter.com/search?q=${trend.currency_name}`"
                    target="_blank"
                  >
                    <p class="p-trend__name">
                      {{ trend.currency_name }}
                    </p>
                  </a>
                  <a
                    :href="`https://twitter.com/search?q=${trend.currency_ja}`"
                    target="_blank"
                  >
                    <span class="p-trend__name--small">
                      {{ trend.currency_ja }}
                    </span>
                  </a>
                </td>
                <td class="p-trend__tweet-td">
                  <p
                    v-if="column === 'tweet_hour'"
                    class="u-font__num"
                  >
                    {{ trend.tweet_hour | localeNum }}
                  </p>
                  <p
                    v-if="column === 'tweet_day'"
                    class="u-font__num"
                  >
                    {{ trend.tweet_day | localeNum }}
                  </p>
                  <p
                    v-if="column === 'tweet_week'"
                    class="u-font__num"
                  >
                    {{ trend.tweet_week | localeNum }}
                  </p>
                </td>
                <td class="p-trend__price-td">
                  <p
                    v-if="trend.high"
                    class="p-trend__price"
                  >
                    <span class="u-font__num">{{ trend.high | round | localeNum }}</span>
                  </p>
                  <p
                    v-else
                    class="u-font--small u-font--muted"
                  >
                    
                  </p>
                </td>
                <td class="p-trend__price-td">
                  <p
                    v-if="trend.low"
                    class="p-trend__price"
                  >
                    <span class="u-font__num">{{ trend.low | round | localeNum }}</span>
                  </p>
                  <p
                    v-else
                    class="u-font--small u-font--muted"
                  >
                    
                  </p>
                </td>
              </tr>
            </transition-group>
          </table>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import { OK } from '../utility'
import SlideDownComponent from '../components/SlideDownComponent.vue'

export default {
  components: { SlideDownComponent },
  filters: {
    // 
    round(val) {
      return Math.round(val * 10) / 10
    },
    // 
    localeNum(val) {
      return val.toLocaleString()
    },
  },
  data() {
    return {
      column: 'tweet_hour',
      items: [], //
      updatedAt: '', //
      isActive: false, //
      selectedItems: [], // id
    }
  },
  computed: {
    // 
    sortedItems() {
      // items1241
      return _.orderBy(this.items, this.column, 'desc')
    },
    addRankNum() {
      return _.each(this.sortedItems, (item, index) => {
        item.ranking = index + 1 //0+1
      })
    },
    // 
    matchedItems() {
      // this
      const _self = this
      // (v-modelid
      if (this.selectedItems.length) {
        // sortedlodashtrue
        return _.filter(this.addRankNum, function (value) {
          //id(true/false)
          return _.includes(_self.selectedItems, value.id)
        })
      }
      // sorted
      return this.addRankNum
    },

    // 
    activeColumn() {
      if (this.column === 'tweet_hour') {
        return '1'
      } else if (this.column === 'tweet_day') {
        return '24'
      } else if (this.column === 'tweet_week') {
        return '1'
      }
    },
  },
  watch: {
    // $route
    $route: {
      async handler() {
        await this.fetchTrends()
      },
      immediate: true, //
    },
  },
  methods: {
    // axiosAPI
    async fetchTrends() {
      this.$store.commit('loader/setIsLoading', true) //
      const response = await axios.get('/api/trend')
      this.$store.commit('loader/setIsLoading', false) //
      if (response.status !== OK) {
        // 
        this.$store.commit('error/setCode', response.status)
        return false //
      }
      // 
      this.items = response.data.trends
      // 
      this.updatedAt = response.data.updated_at
    },
    // sortedItems()
    sortByHour() {
      this.column = 'tweet_hour'
    },
    sortByDay() {
      this.column = 'tweet_day'
    },
    sortByWeek() {
      this.column = 'tweet_week'
    },
    // 
    deselect() {
      // 
      this.selectedItems = []
    },
    // 
    selectAll() {
      // 
      this.selectedItems = []
      // APIid
      this.selectedItems = _.map(this.items, 'id')
    },
  },
}
</script>
