<template>
  <transition
    name="popup"
    appear
  >
    <div
      v-if="isVisible"
      class="p-pagetop"
      @click="scrollTop()"
    >
      <span class="p-pagetop__btn"><i class="fas fa-chevron-up" /></span>
    </div>
  </transition>
</template>

<script>
export default {
  data() {
    return {
      currentHeight: 0,
    }
  },
  computed: {
    // 200px
    isVisible() {
      return this.currentHeight > 200
    },
  },
  mounted() {
    document.onscroll = () => {
      // 
      this.currentHeight = document.documentElement.scrollTop || document.body.scrollTop
    }
  },
  methods: {
    // 
    scrollTop() {
      // top:0
      window.scrollTo({
        top: 0,
        behavior: 'smooth',
      })
    },
  },
}
</script>
