import { OK, CREATED, UNPROCESSABLE_ENTITY } from '../utility'

// auth
// Vuex

// 
const state = {
  userData: null, //
  apiStatus: null, //API
  registerErrorMessages: null, //
  loginErrorMessages: null, //
  editErrorMessages: null, //
}

// 
const getters = {
// 
  check: (state) => !!state.userData, //userDatatrue
  userName: (state) => (state.userData ? state.userData.name : ''), //null
}

// 
// 
const mutations = {
// userData
  setUserData(state, userData) {
    state.userData = userData
  },
  // apiStatus
  setApiStatus(state, status) {
    state.apiStatus = status
  },
  // registerErrorMessages
  setRegisterErrorMessages(state, messages) {
    state.registerErrorMessages = messages
  },
  // loginErrorMessages
  setLoginErrorMessages(state, messages) {
    state.loginErrorMessages = messages
  },
  // editErrorMessages
  setEditErrorMessages(state, messages) {
    state.editErrorMessages = messages
  },
}

// commit
const actions = {
/**
 * 
 */
  // commit()
  async register(context, data) {
    // setApiStatus
    context.commit('setApiStatus', null)

    // API
    const response = await axios.post('/api/register', data)
    // catchbootstrap.jsaxios
    // .catch(err => err.response || err)
    // API
    if (response.status === CREATED) {
      // setApiStatustrue
      context.commit('setApiStatus', true)
      // setUserDatauserData
      context.commit('setUserData', response.data)
      return false //
    }
    // APIfalse
    context.commit('setApiStatus', false)

    // 
    if (response.status === UNPROCESSABLE_ENTITY) {
      context.commit('setRegisterErrorMessages', response.data.errors)
    } else {
      // errorsetCode
      // commitroot: true
      context.commit('error/setCode', response.status, { root: true })
    }
  },

  /**
 * 
 */
  async login(context, data) {
    // setApiStatus
    context.commit('setApiStatus', null)
    // API
    const response = await axios.post('/api/login', data)

    // API
    if (response.status === OK) {
      // setApiStatustrue
      context.commit('setApiStatus', true)
      // setUserDatauserData
      context.commit('setUserData', response.data)

      // dispatch()twitterupdateTwitterUser
      // root: true
      context.dispatch('twitter/updateTwitterUser', '', { root: true })
      return false //
    }
    // APIfalse
    context.commit('setApiStatus', false)

    // 
    if (response.status === UNPROCESSABLE_ENTITY) {
      context.commit('setLoginErrorMessages', response.data.errors)
    } else {
      // errorsetCode
      // commitroot: true
      context.commit('error/setCode', response.status, { root: true })
    }
  },

  /**
 * 
 */
  async logout(context) {
    // setApiStatus
    context.commit('setApiStatus', null)
    // API
    const response = await axios.post('/api/logout')

    // API
    if (response.status === OK) {
      // setApiStatustrue
      context.commit('setApiStatus', true)
      // setUserDatauserDatanull
      context.commit('setUserData', null)
      // twitternull
      context.commit('twitter/setUsersTwitter', null, { root: true })
      return false //
    }
    // APIfalse
    context.commit('setApiStatus', false)
    // errorsetCode
    // commitroot: true
    context.commit('error/setCode', response.status, { root: true })
  },

  /**
 * 
 */
  async currentUser(context) {
    // setApiStatus
    context.commit('setApiStatus', null)
    // API
    const response = await axios.post('/api/user')

    // null
    const userData = response.data || null

    // API
    if (response.status === OK) {
      // setApiStatustrue
      context.commit('setApiStatus', true)
      // setUserDatauserData
      context.commit('setUserData', userData)
      return false //
    }
    // APIfalse
    context.commit('setApiStatus', false)
    // errorsetCode
    // commitroot: true
    context.commit('error/setCode', response.status, { root: true })
  },

  /**
 * 
 */
  async EditAccount(context, data) {
    // setApiStatus
    context.commit('setApiStatus', null)

    // API
    const response = await axios.post('/api/account/change', data)

    // API
    if (response.status === OK) {
      // setApiStatustrue
      context.commit('setApiStatus', true)
      // setUserDatauserData
      context.commit('setUserData', response.data)
      return false //
    }
    // APIfalse
    context.commit('setApiStatus', false)

    // 
    if (response.status === UNPROCESSABLE_ENTITY) {
      context.commit('setEditErrorMessages', response.data.errors)
    } else {
      // errorsetCode
      // commitroot: true
      context.commit('error/setCode', response.status, { root: true })
    }
  },

  /**
 * 
 */
  async withdraw(context, data) {
    // setApiStatus
    context.commit('setApiStatus', null)

    // API
    const response = await axios.post('/api/account/withdraw', data)

    // API
    if (response.status === OK) {
      // setApiStatustrue
      context.commit('setApiStatus', true)
      // setUserDatauserData
      context.commit('setUserData', null)
      // twitternull
      context.commit('twitter/setUsersTwitter', null, { root: true })
      return false //
    }
    // APIfalse
    context.commit('setApiStatus', false)
    // errorsetCode
    // commitroot: true
    context.commit('error/setCode', response.status, { root: true })
  },
}

// 
export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
}
