<template>
  <div class="c-container--bg">
    <h2 class="c-container__title"></h2>
    <div class="c-form__wrapper">
      <form class="c-form--small" @submit.prevent="checkForm">
        <div class="c-form__group">
          <label for="login-email" class="c-form__label">
            
          </label>
          <input
            id="login-email"
            v-model="loginForm.email"
            type="email"
            class="c-input c-input--large"
            required
            autocomplete="email"
            autofocus
          />
          <invalid-component :messages="emailErrors" />
          <invalid-component
            v-if="loginErrors && loginErrors.email"
            :messages="loginErrors.email"
          />
        </div>
        <div class="c-form__group">
          <label for="login-password" class="c-form__label"></label>
          <input
            id="login-password"
            v-model="loginForm.password"
            type="password"
            class="c-input c-input--large"
            required
            autocomplete="current-password"
          />
          <div class="u-font--right">
            <router-link
              :to="{ name: 'password.request' }"
              class="c-form__link"
            >
              
            </router-link>
          </div>
          <invalid-component :messages="passwordErrors" />
          <invalid-component
            v-if="loginErrors && loginErrors.password"
            :messages="loginErrors.password"
          />
        </div>
        <div class="c-form__group">
          <label class="c-checkbox__label" for="remember">
            <input
              id="remember"
              v-model="loginForm.remember"
              class="c-checkbox__input"
              type="checkbox"
            />
            <span class="c-checkbox__dummyInput" />
            <span class="c-checkbox__text"></span></label
          >
        </div>

        <div class="c-form__button">
          <button type="submit" class="c-btn--main-outline c-btn--large">
            
          </button>
        </div>
      </form>
      <div class="c-form__link">
        <router-link :to="{ name: 'register' }" class="c-form__link">
          
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex"; // VuexmapState
import InvalidComponent from "../components/InvalidComponent.vue";

export default {
  components: {
    InvalidComponent, //
  },
  data() {
    return {
      // v-model
      loginForm: {
        email: "",
        password: "",
        remember: "",
      },
      emailErrors: [],
      passwordErrors: [],
    };
  },
  computed: {
    ...mapState({
      // authAPI
      apiStatus: (state) => state.auth.apiStatus,
      // auth
      loginErrors: (state) => state.auth.loginErrorMessages,
    }),
  },
  created() {
    // 
    this.clearError();
  },
  methods: {
    // 
    checkForm() {
      const MSG_EMAIL_EMPTY = "";
      const MSG_EMAIL_TYPE = "";
      const MSG_EMAIL_MAX = "50";
      const MSG_PASS_EMPTY = "";
      const MSG_PASS_LESS = "";

      this.emailErrors = [];
      this.passwordErrors = [];

      // 
      if (!this.loginForm.email) {
        // 
        this.emailErrors.push(MSG_EMAIL_EMPTY);
      } else if (this.loginForm.email.length > 50) {
        // 
        this.emailErrors.push(MSG_EMAIL_MAX);
      } else if (!this.validEmail(this.loginForm.email)) {
        // 
        this.emailErrors.push(MSG_EMAIL_TYPE);
      }
      // 
      if (!this.loginForm.password) {
        // 
        this.passwordErrors.push(MSG_PASS_EMPTY);
      } else if (this.loginForm.password.length < 8) {
        // 
        this.passwordErrors.push(MSG_PASS_LESS);
      }
      // 
      const results = this.emailErrors.concat(this.passwordErrors);
      // WebAPI
      if (!results.length) {
        this.login();
      }
    },
    // 
    validEmail(email) {
      const regex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return regex.test(email);
    },

    // WebAPI
    async login() {
      // dispatch()authlogin
      await this.$store.dispatch("auth/login", this.loginForm);
      // API
      if (this.apiStatus) {
        // 
        this.$store.dispatch("message/showMessage", {
          text: "",
          type: "success",
          timeout: 2000,
        });
        // VueRouterpush()
        this.$router.push({ name: "home" });
      }
    },
    clearError() {
      this.$store.commit("auth/setLoginErrorMessages", null);
    },
  },
};
</script>
