<template>
  <div class="c-container--bg">
    <section class="c-section">
      <h5 class="c-section__title"></h5>
      <p class="c-section__text">
        <br
          class="u-sp--only"
        /><br />
      </p>
      <div class="p-news c-fade-in">
        <pagination-info
          :current-page="currentPage"
          :per-page="perPage"
          :total-num="totalNum"
          :items-length="news.length"
        />
        <search-form-component
          :total-num="totalNum"
          :searched-param="searchedParam"
          item-name=""
          @search="searchNews"
          @clear="clearResult"
        />

        <transition-group tag="div" name="popup" appear class="p-news__item">
          <div v-for="item in news" :key="item.title" class="p-news__item">
            <div class="p-news__info">
              <span class="p-news__date">
                {{ item.published_date }}
              </span>
              <span class="p-news__source">
                {{ item.source }}
              </span>
            </div>
            <h5 class="p-news__title">
              <a :href="item.url" target="_blank">
                {{ item.title }}
              </a>
            </h5>
          </div>
        </transition-group>

        <div class="c-pagination">
          <pagination-link
            :directory="directoryName"
            :searched-param="searchedParam"
            :current-page="currentPage"
            :last-page="lastPage"
            :per-page="perPage"
            :total-num="totalNum"
          />
          <pagination-info
            :current-page="currentPage"
            :per-page="perPage"
            :total-num="totalNum"
            :items-length="news.length"
          />
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import { OK } from "../utility";
import PaginationLink from "../components/PaginationLink.vue";
import PaginationInfo from "../components/PaginationInfo.vue";
import SearchFormComponent from "../components/SearchFormComponent.vue";

export default {
  components: {
    PaginationLink, //PC
    PaginationInfo, //
    SearchFormComponent, //
  },
  // router.js
  props: {
    page: {
      type: Number,
      required: false,
      default: 1,
    },
  },
  data() {
    return {
      news: [], //
      currentPage: 0, //
      lastPage: 0, //
      perPage: 0, //1
      totalNum: 0, //
      directoryName: "news", //
      searchedParam: "", //
    };
  },
  watch: {
    // 
    // $route
    $route: {
      async handler() {
        await this.fetchNews();
      },
      immediate: true, //
    },
  },
  methods: {
    // 
    searchNews(word) {
      // 
      if (!word) {
        this.clearResult();
        return;
      }
      let params = {
        // 
        params: {
          search: word,
        },
      };
      // 
      this.fetchNews(params);
      this.searchedParam = word;
    },
    // 
    clearResult() {
      let params = {
        // 
        params: {
          page: 1,
        },
      };
      this.fetchNews(params);
      // propssearchedParam
      this.searchedParam = "";
    },
    // axiosAPI
    async fetchNews(params) {
      if (!params) {
        // 
        params = {
          params: {
            page: this.page,
            search: this.searchedParam,
          },
        };
      }
      this.$store.commit("loader/setIsLoading", true); //
      // API
      const response = await axios.get("/api/news", params);
      this.$store.commit("loader/setIsLoading", false); //
      if (response.status !== OK) {
        // 
        this.$store.commit("error/setCode", response.status);
        return false; //
      }
      // JSONdata
      this.news = response.data.data;

      // 
      this.currentPage = response.data.current_page;
      this.lastPage = response.data.last_page;
      // 1
      this.perPage = response.data.per_page;
      this.totalNum = response.data.total;
      return;
    },
  },
};
</script>
