<template>
  <div class="c-search">
    <form
      class="c-search__form"
      @submit.prevent="search"
    >
      <span class="c-search__icon"><i class="fas fa-search" /></span>
      <input
        v-model="searchWord"
        type="text"
        class="c-input c-search__input"
        name="search"
        placeholder=""
      >
      <span class="c-search__clear-icon">
        <i
          v-show="searchWord"
          class="fas fa-times"
          @click="clearWord"
        />
      </span>
    </form>
    <div
      v-if="searchedParam && totalNum === 0"
      class="c-search__notfound"
    >
      <p class="u-font--center">
        {{ searchedParam }}{{ itemName }}
      </p>
    </div>
    <div
      v-if="searchedParam && totalNum > 0"
      class="c-search__found u-mb--xl"
    >
      <p class="u-font--center">
        {{ searchedParam }}{{ itemName }}<br class="u-sp--only">
        {{ totalNum }}
      </p>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    totalNum: {
      type: Number,
      required: true,
    },
    searchedParam: {
      type: String,
      required: true,
    },
    itemName: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      searchWord: '', // v-model
    }
  },
  methods: {
    search() {
      this.$emit('search', this.searchWord)
    },
    clearWord() {
      this.searchWord = ''
      this.$emit('clear')
    },
  },
}
</script>
