<template>
  <p
    v-if="totalNum"
    class="c-pagination__info"
  >
    {{ totalNum }} {{ startNum }} - {{ endNum }}
  </p>
</template>

<script>
export default {
  props: {
    // 
    currentPage: {
      type: Number,
      required: true,
    },
    //1
    perPage: {
      type: Number,
      required: true,
    },
    //
    totalNum: {
      type: Number,
      required: true,
    },
    //
    itemsLength: {
      type: Number,
      required: true,
    },
  },
  computed: {
    // 1
    startNum() {
      // 00
      if (this.totalNum === 0) {
        return 0
      }
      // 1  11
      return this.perPage * (this.currentPage - 1) + 1
    },
    // 
    endNum() {
      // 00
      if (this.totalNum === 0) {
        return 0
      }
      // 1  11
      return this.perPage * (this.currentPage - 1) + this.itemsLength
    },
  },
}
</script>
