<template>
  <div class="c-container--bg">
    <h2 class="c-container__title">
      
    </h2>
    <div class="c-form__wrapper">
      <contact-form
        v-if="!isConfirm && !isSent"
        :form-data="formData"
        @confirm="formConfirm"
      />
      <contact-confirm
        v-if="isConfirm"
        :form-data="formData"
        @sent="formSent"
        @back="formBack"
      />
      <contact-sent
        v-if="isSent"
        :form-data="formData"
      />
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex"; // VuexmapState
import ContactForm from "../components/ContactForm.vue";
import ContactConfirm from "../components/ContactConfirm.vue";
import ContactSent from "../components/ContactSent.vue";

export default {
  components: {
    // ViewRouter
    ContactForm, //
    ContactConfirm, //
    ContactSent, //
  },
  data() {
    return {
      // v-model
      // props
      formData: {
        name: "",
        email: "",
        message: "",
      },
      isConfirm: false,
      isSent: false,
    };
  },
  computed: {
    ...mapState({
      // auth
      userData: (state) => state.auth.userData,
    }),
  },
  created() {
    // 
    this.setUserData();
  },
  methods: {
    setUserData() {
      // 
      if (this.userData) {
        // DBv-model
        this.formData.name = this.userData.name ?? "";
        this.formData.email = this.userData.email ?? "";
      }
    },
    formConfirm(contactForm) {
      // v-model
      Object.assign(this.formData, contactForm);
      //
      this.isConfirm = true;
      this.isSent = false;
    },
    formSent() {
      //
      this.isSent = true;
      this.isConfirm = false;
    },
    formBack() {
      //
      this.isSent = false;
      this.isConfirm = false;
    },
  },
};
</script>
